.menu-trigger {
  position: relative;
  top: 21px;
  padding-right: 52px;
  width: 25px;
  height: 25px;
  display: inline-block;
  cursor: pointer;
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  text-align: justify;
}
@media screen and (max-width: 729px) {
  .menu-trigger {
    position: absolute;
    left: 15px;
    top: 22px;
  }
}
.menu-trigger .icon-left {
  position: absolute;
  width: 15px;
  height: 4px;
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-left:before {
  position: absolute;
  top: -10px;
  width: 15px;
  height: 4px;
  content: '';
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-left:after {
  position: absolute;
  top: 10px;
  width: 15px;
  height: 4px;
  content: '';
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-left:hover {
  cursor: pointer;
}
.menu-trigger .icon-right {
  position: absolute;
  left: 15px;
  width: 15px;
  height: 4px;
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-right:before {
  position: absolute;
  top: -10px;
  width: 15px;
  height: 4px;
  content: '';
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-right:after {
  position: absolute;
  top: 10px;
  width: 15px;
  height: 4px;
  content: '';
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background-color: #fff;
}
.menu-trigger .icon-right:hover {
  cursor: pointer;
}
.menu-trigger.active .icon-left {
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background: transparent;
}
.menu-trigger.active .icon-left:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px);
  -ms-transform: rotate(45deg) scaleX(1.4) translate(2px, 2px);
  transform: rotateZ(45deg) scaleX(1.4) translate(2px, 2px);
}
.menu-trigger.active .icon-left:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px);
  -ms-transform: rotate(-45deg) scaleX(1.4) translate(2px, -2px);
  transform: rotateZ(-45deg) scaleX(1.4) translate(2px, -2px);
}
.menu-trigger.active .icon-right {
  -webkit-transition-duration: .5s;
  transition-duration: .5s;
  background: transparent;
}
.menu-trigger.active .icon-right:before {
  -webkit-transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px);
  -ms-transform: rotate(-45deg) scaleX(1.4) translate(-2px, 2px);
  transform: rotateZ(-45deg) scaleX(1.4) translate(-2px, 2px);
}
.menu-trigger.active .icon-right:after {
  -webkit-transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px);
  -ms-transform: rotate(45deg) scaleX(1.4) translate(-2px, -2px);
  transform: rotateZ(45deg) scaleX(1.4) translate(-2px, -2px);
}
