.singlepage-nav {
  background: #107e3c;


  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    min-height: 41px;
    padding: 6px 0 0;

    li {
      display: inline;
      margin-left: 45px;

      a {
        text-decoration: none;
        color: white;
        text-transform: uppercase;
        font-family: Brandongm;
        font-weight: 900;
        letter-spacing: 0.1em;
        padding: 0;
        line-height: 1.5em;
        vertical-align: middle;

        .fa-stack {
          margin-top: -6px;
          width: 1.5em;
          height: 1.5em;
          line-height: 1.5em;
        }

        .fa-stack-2x {
          font-size: 1.5em;
        }
        .fa-inverse {
          color: #107e3c;
          font-size: 0.7em;
        }
      }


    }
  }
}